
@font-face {
    font-family: 'Neo Sans Arabic';
    src: url('../assets/NeoSansArabic.woff2') format('woff2'),
        url('../assets/NeoSansArabic.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
.application {
        font-family: 'Neo Sans Arabic'; font-weight: normal; font-style: normal;
}
body {
      background-color: #E53F35;
}
#app {
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      text-align: center;
      color: #cad6e6;
}
a {
        color: #6edbff;
&:hover, &:focus {
            color: lighten(#6edbff, 10);
}
}
header {
        position: relative;
        padding: 2rem 0 2rem;
        margin-bottom: 3rem;
        overflow: hidden;
}
.wrapper {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  -webkit-box-align: center;
          align-items: center; /* Horizontal alignment */
}
@media only screen and (max-device-width : 640px) {
h3 {
    font-size: 4vw !important;
}
}
h1 {
    font-weight: bold;
  margin-bottom: 0px;
  position: relative;
  z-index: 5;
}
h1>.boxy {
  border-style: solid;
  border-width: 4px 4px 0 4px; /* No border at the bottom */
  box-shadow: -10px -10px;
}
h3>.boxy {
  border-style: solid;
  border-width: 4px;
  box-shadow: -10px -10px;
}
.boxy {
  background-color: white;
  border-radius: 2px;
  color: black;
  padding: 10px;
}
button {
  border-style: solid;
  border-color: black;
  border-width: 4px;
  box-shadow: 10px 10px !important;
  text-transform: lowercase !important;
}
button:hover {
  box-shadow: 0px 0px !important;
}
.bg-img {
  position: absolute; width: 100%; left: 0;
}



